<template>
	<div id="Job">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">招聘求职</div>
      </div>
    </div>
    <div class="login_box text_center">
      <div style="margin-top: 100px">
        <img src="static/img/icon_wyzp_zpqz.png" style="width: 30%;height: 30%;" @click="toAddJob"/>
        <br>
        <span>我要招聘</span>
      </div>
      <div style="margin-top: 50px">
        <img src="static/img/icon_wyqz_zpqz.png" style="width: 30%;height: 30%;" @click="toAddResume"/>
        <br>
        <span>我要求职</span>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'Job',
		data() {
			return {

			}
		},
		methods:{
      toAddResume(){
        this.$router.push('addResume');
      },
      toAddJob(){
        this.$router.push('addJob');
      },
		}
	}
</script>
<style scoped>
  .login_box{
    margin-top: 50px;
  }
</style>



